.container {
    width: 100%;
    max-width: 1400px;
    margin: 0 auto;
    padding: 0 15px;
}
.row {
    display: flex;
    flex-wrap: wrap;
}
$columns: 12;
$column-base-width: 100% / $columns;
@for $i from 1 through $columns {
    .col-#{$i} {
      min-height: 1px;
      width: $column-base-width * $i;
      //border: 1px solid black;
    }
}
  
[class^="col-"]{
    @include breakpoint(xs){
      width: 100%;
    }
}